import React from 'react';
import {useState, useRef} from 'react';

import './App.css';

const IFRAME_URL = 'https://www.internalfb.com/intern/quip2google/format/';

function App(props) {
  const [isIFrameLoading, setIsIFrameLoading] = useState(true);
  const iframeRef = useRef(null);
  const google_doc_id = props.params.google_doc_id;
  return (
    <div className="App">
      {isIFrameLoading ? (
        <div className="AppLoader">
          <div>
            <h1>Loading Quip2Google...</h1>
          </div>
        </div>
      ) : null}
      <iframe
        ref={iframeRef}
        onLoad={() => {
          setIsIFrameLoading(false);
        }}
        className="AppIFrame"
        title="iframe"
        style={{
          display: isIFrameLoading ? 'none' : 'block',
        }}
        frameBorder="0px"
        src={IFRAME_URL + '?google_doc_id=' + google_doc_id}
      />
    </div>
  );
}

export default App;
