import React from 'React';
import {createRoot} from 'react-dom/client';
import App from './App';

function genCurrentPageURL(callback) {
  chrome.tabs.query({active: true, currentWindow: true}, tabs => {
    callback(tabs[0].url ?? '');
  });
}

const rootElement = document.getElementById('root');
const root = createRoot(rootElement!);
const pattern = /docs\.google\.com\/document\/d\/(.*)\//;
genCurrentPageURL((pageURL: string) => {
  const match = pageURL.match(pattern);
  if (match && match.length > 1) {
    const google_doc_id = match[1];
    root.render(<App params={{google_doc_id: google_doc_id}} />);
  } else {
    root.render(<div>Please open a Google Doc</div>);
  }
});
